<template>
    <view>
        <m-common title="模态框" />
        <m-cell-group border>
            <m-cell
                title="淡入"
                is-link
                @click="handleClick(0)"
            />
            <m-cell
                title="上滑淡入"
                is-link
                @click="handleClick(1)"
            />
            <m-cell
                title="下滑淡入"
                is-link
                @click="handleClick(2)"
            />
            <m-cell
                title="左滑淡入"
                is-link
                @click="handleClick(3)"
            /><m-cell
                title="右滑淡入"
                is-link
                @click="handleClick(4)"
            /><m-cell
                title="上滑进入"
                is-link
                @click="handleClick(5)"
            /><m-cell
                title="下滑进入"
                is-link
                @click="handleClick(6)"
            /><m-cell
                title="左滑进入"
                is-link
                @click="handleClick(7)"
            /><m-cell
                title="右滑进入"
                is-link
                @click="handleClick(8)"
            /><m-cell
                title="缩放"
                is-link
                @click="handleClick(9)"
            /><m-cell
                title="缩放淡入"
                is-link
                @click="handleClick(10)"
            />
        </m-cell-group>

        <m-transition
            :show="show"
            :mode="mode"
            :custom-style="style"
        >
            <view class="mask" />
        </m-transition>
    </view>
</template>

<script lang="ts" setup>
import { ref } from 'vue'

const show = ref(false)
const mode = ref('fade')
const style= {
    position: 'fixed',
    top: `${uni.$m.sys().windowHeight / 2 - 50}px`,
    left: `${uni.$m.sys().windowWidth / 2 - 50}px`,
    width: '120px',
    height: '120px',
    backgroundColor: '#1989fa',
}

const handleClick = index => {
    const obj = {
        0: 'fade',
        1: 'fade-up',
        2: 'fade-down',
        3: 'fade-left',
        4: 'fade-right',
        5: 'slide-up',
        6: 'slide-down',
        7: 'slide-left',
        8: 'slide-right',
        9: 'zoom',
        10: 'fade-zoom',
    }
    show.value = true
    mode.value = obj[index]
    setTimeout(() => {
        show.value = false
    }, 500)
}
</script>

<style lang="scss" scoped>
.mask {
    background-color: $m-primary;
}
</style>
